<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
      If you want to delete your account. Please click the steps below:<br>
      1. Enter your registered account<br>
      2. Enter your password when you registered<br>
      3. Click Delete<br>
      Tip: After the operation is successful, your account information will be deleted immediately, including your favorites, your viewing history information, coins, etc.<br>
      If you want to continue using. In the APP, you can re-register for the same use.<br>
      <a-divider />
      如果你想删除你的帐户 请点击以下程序：<br>
        1. 输入您的注册帐号<br>
        2.注册时输入密码<br>
        3. 点击删除<br>
        提示：操作成功后 您的帐户信息将立即被删除。 包括收藏夹 查看历史数据、币种等<br>
        如果您想继续使用它。 在应用程序中，您可以重新注册相同的用途<br>
       <a-divider />

       <div class="components-input-demo-presuffix">
  
        <a-space direction="vertical">
          <a-input v-model:value="phone" addon-before="+" placeholder="Account Phone" />
          <a-input-password v-model:value="pwd" placeholder="Password" />
          <a-button danger block @click="deleteAccount">Delete Account</a-button>
        </a-space>
      </div>
  </div>
</template>

<script>

import { message } from 'ant-design-vue';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data: () => ({
    phone:"",
    pwd:""
  }),
  methods: {
    deleteAccount() {
      if (this.phone.length < 4) {
        message.error('Account Phone is error input');
      } else if (this.pwd.length < 4) {
        message.error('Password is errror input');
      } else {
        const data = 
        fetch("https://api.tvtvd.com/meta/user/deleteAccount?phone=" + this.phone + "&pwd=" + this.pwd)
        .then((response) => {
          // 取出响应中的json数据 *
          return response.json();
        })
        .then(() => {
          console.log(data)
          message.success('Account is deleted!');
          this.phone = ""
          this.pwd = ""
        })
        .catch(error => {    // 请求失败处理
            console.log(error);
            message.success('Account is deleted!');
            this.phone = ""
            this.pwd = ""
        })
        .then(() => {
          this.loading = false;
        });
      }
    }
  }
}
</script>

